import React, { MouseEvent } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Popover } from '@material-ui/core';

type SignatureProps = {
    signature: string;
    id: string;
}

const useStyles = makeStyles((theme) => ({
    typography: {
        padding: theme.spacing(2),
    },
}));

const Signature = (props: SignatureProps) => {
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);

    const handleClick = (event: MouseEvent<HTMLButtonElement, globalThis.MouseEvent>) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const open = Boolean(anchorEl);
    const { id, signature } = props;

    return (
        <div>
            <Button aria-describedby={id} variant="contained" onClick={handleClick}>
                <img src={`data:image/jpeg;base64,${signature}`} width={30} />
            </Button>
            <Popover
                id={id}
                open={open}
                anchorEl={anchorEl}
                onClose={handleClose}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
            >
                <img src={`data:image/jpeg;base64,${signature}`} width={180} />
            </Popover>
        </div>
    );
}
export default Signature;