import { get } from "./api";

export const getKey = async () => {
  var result = await get<string>("unique-key");
  return result;
};

export type HistoryItem = {
  firstname: string;
  lastname: string;
  organisation: string;
  type: "Check In" | "Check Out";
  signature: string;
  timestamp: string;
};
export const getHistory = async () => {
  var result = await get<HistoryItem[]>("history");
  return result;
};
