import axios from "axios";

export const setAuthToken = (token: string) => {
  axios.defaults.headers.common["Authorization"] = token;
};

export const initialize = (apiRootUrl: string) => {
  axios.defaults.baseURL = apiRootUrl;

  // https://medium.com/@monkov/react-using-axios-interceptor-for-token-refreshing-1477a4d5fc26
  axios.interceptors.response.use(
    (response) => {
      return response;
    },
    (err) => {
      return new Promise((resolve, reject) => {
        const originalReq = err.config;
        if (
          err.response &&
          err.response.status === 401 &&
          err.config &&
          !err.config.__isRetryRequest
        ) {
          originalReq._retry = true;

          let res = fetch(`${apiRootUrl}/refresh`, {
            method: "POST",
            mode: "cors",
            cache: "no-cache",
            credentials: "same-origin",
            headers: {
              "Content-Type": "application/json",
              Accept: "application/json",
            },
            redirect: "follow",
            referrer: "no-referrer",
          })
            .then((res) => res.json())
            .then((res) => {
              originalReq.headers["Token"] = res.token;
              originalReq.headers["Device"] = "device";

              return axios(originalReq);
            });

          resolve(res);
        }

        return Promise.reject(err);
      });
    }
  );
};

export async function get<TReturn>(path: string) {
  const { get: getInternal } = axios;
  var res = await getInternal(path);
  return res.data as TReturn;
}

export async function post<TData, TReturn>(path: string, data: TData) {
  const { post: postInternal } = axios;
  const res = await postInternal(path, data);
  console.log(res.data);
  return res.data as TReturn;
}
