import React from "react";

import { render } from "react-dom";
import { createBrowserHistory } from "history";

import App from "./app";
import configureStore, { ApplicationState } from "./store";
import { initialize } from "./utils/api";

window.global.settings = JSON.parse(window.global.__SETTINGS__);
initialize(window.global.settings.apiRootUrl);

const history = createBrowserHistory({ basename: "admin" });
const initState: ApplicationState = {
    router: {
        location: history.location,
        action: history.action,
    },
};
const store = configureStore(history, initState);
render(
    <App store={store} history={history} />,
    document.getElementById("root")
);
