import React, { useState } from 'react';
import { withRouter, Route, Switch, Redirect, NavLink, useLocation } from 'react-router-dom';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import HomeIcon from '@material-ui/icons/Home';
import ListIcon from '@material-ui/icons/List';

import {
    CssBaseline,
    AppBar,
    Toolbar,
    Typography,
    IconButton,
    Drawer,
    MenuItem,
    ListItemText,
    ListItemIcon,
    List,
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import CheckinList from './checkin-list'
import OrganizationQRCode from './org-info-qrcode';

const drawerWidth = 240;
const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    appBar: {
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth,
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    drawerHeader: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
        justifyContent: 'flex-end',
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        marginLeft: -drawerWidth,
    },
    contentShift: {
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        marginLeft: 0,
    },
}));

const RoutePath = {
    Home: "/",
    Qrcode: "/qrcode",
    CheckinList: "/checkin-list"
};

const Layout = (props: {}) => {
    const classes = useStyles();
    const theme = useTheme();
    const [open, setOpen] = useState(true);

    const { pathname } = useLocation();
    const activeRoute = (routeName: string) => {
        console.log(`${pathname}  ${routeName} ${pathname === routeName}`)
        return pathname === routeName;
    }

    const handleDrawerOpen = () => {
        setOpen(true);
    };
    const handleDrawerClose = () => {
        setOpen(false);
    };

    return <div className={classes.root}>
        <CssBaseline />
        <AppBar
            position="fixed"
            className={clsx(classes.appBar, { [classes.appBarShift]: open, })}
        >
            <Toolbar>
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    onClick={handleDrawerOpen}
                    edge="start"
                    className={clsx(classes.menuButton, open && classes.hide)}
                >
                    <MenuIcon />
                </IconButton>
                <Typography variant="h6" noWrap>Persistent drawer</Typography>
            </Toolbar>
        </AppBar>
        <Drawer className={classes.drawer}
            variant="persistent"
            anchor="left"
            open={open}
            classes={{ paper: classes.drawerPaper }}
        >
            <div className={classes.drawerHeader}>
                <IconButton onClick={handleDrawerClose}>
                    {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
                </IconButton>
            </div>
            <List>
                <NavLink to={RoutePath.Home}
                    style={{ textDecoration: 'none', color: 'inherit' }} >
                    <MenuItem selected={activeRoute(RoutePath.Home) || activeRoute(RoutePath.Qrcode)}>
                        <ListItemIcon>
                            <HomeIcon />
                        </ListItemIcon>
                        <ListItemText primary={"Home"} />
                    </MenuItem>
                </NavLink>

                <NavLink to={RoutePath.CheckinList}
                    style={{ textDecoration: 'none', color: 'inherit' }} >
                    <MenuItem selected={activeRoute(RoutePath.CheckinList)}>
                        <ListItemIcon>
                            <ListIcon />
                        </ListItemIcon>
                        <ListItemText primary={"Checkin List"} />
                    </MenuItem>
                </NavLink>
            </List>
        </Drawer>
        <main className={clsx(classes.content, { [classes.contentShift]: open, })}>
            <div className={classes.drawerHeader} />
            <Switch>
                <Redirect exact from={RoutePath.Home} to={RoutePath.Qrcode} />
                <Route path={RoutePath.Qrcode} component={OrganizationQRCode} />
                <Route exact path={RoutePath.CheckinList} component={CheckinList} />
            </Switch>
        </main>
    </div>
}

export default withRouter(Layout);