import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import { getHistory, HistoryItem } from '../utils/rest'
import Signature from './signature'

const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
});

type SignatureData = {
    time: Date,
    signature: string
}

type CheckinInfo = {
    name: string,
    checkin?: SignatureData,
    checkout?: SignatureData,
}

const toCheckinInfo = (source: HistoryItem[]): CheckinInfo[] => {
    var result = source.reduce((previousResult, currentItem) => {
        const currentName = `${currentItem.firstname} ${currentItem.lastname}`;

        let result: CheckinInfo[] = [...previousResult];
        var info = previousResult.find(x => x.name === currentName);
        if (!info) {
            info = { name: currentName };
            result = [...previousResult, info];
        }
        if (currentItem.type === 'Check In') {
            info.checkin = {
                time: new Date(currentItem.timestamp),
                signature: currentItem.signature
            }
        }
        if (currentItem.type === 'Check Out') {
            info.checkout = {
                time: new Date(currentItem.timestamp),
                signature: currentItem.signature
            }
        }

        return result;
    }, [] as CheckinInfo[])
    return result;
}

const CheckinList = () => {
    const classes = useStyles();

    const [rows, setRows] = useState<CheckinInfo[]>([]);
    useEffect(() => {
        (async function () {
            var history = await getHistory();
            setRows(toCheckinInfo(history));
        })();
    }, []);

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell>Name</TableCell>
                        <TableCell align="right">Checkin Time</TableCell>
                        <TableCell align="right">Checkin Signature</TableCell>
                        <TableCell align="right">Checkout Time</TableCell>
                        <TableCell align="right">Checkout Signature</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row, index) => (
                        <TableRow key={row.name}>
                            <TableCell component="th" scope="row">
                                {row.name}
                            </TableCell>
                            <TableCell align="right">{row.checkin?.time.toLocaleString()}</TableCell>
                            <TableCell align="right">
                                {
                                    row.checkin?.signature && <Signature id={`checkin-${index}`} signature={row.checkin.signature} />
                                }
                            </TableCell>
                            <TableCell align="right">{row.checkout?.time.toLocaleString()}</TableCell>
                            <TableCell align="right">
                                {
                                    row.checkout?.signature && <Signature id={`checkout-${index}`} signature={row.checkout.signature} />
                                }
                            </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}

export default CheckinList;